// Âè™ÈÄÇÁî®‰∫éÁßªÂä®Á´ØÔºåÊ≤°ÊúâÂ§ÑÁêÜpcÁ´ØÁöÑÊòæÁ§∫ÔºåÂú®pcÁ´ØÈ°µÈù¢‰ºöÊòæÁ§∫Âú∞ÂæàÂ§ß„ÄÇ
(function (window, html) {
  // ÈªòËÆ§ÁöÑËÆæËÆ°Á®øÂÆΩÂ∫¶720px
  const designWidth = 375;

  // var windowWidth = html.clientWidth < designWidth ? html.clientWidth : designWidth;
  // Ëé∑ÂèñÈ°µÈù¢ÁöÑÂÆΩÂ∫¶

  function recalc() {
    const windowWidth = html.clientWidth;
    console.log(`windowWidth`, windowWidth);

    if (windowWidth >= 320 && windowWidth <= 1024) {
      // ÊàëÁöÑÁêÜËß£ÊòØÔºö Â∞ÜÈ°µÈù¢Á≠âÂàÜÊàê designWidth ‰ªΩ
      // ËøôÊ†∑ÁöÑËØùÔºåËÆæËÆ°Á®ø‰∏≠ÁöÑ1px Â∞±Á≠â‰∫é 1remÔºåÊñπ‰æøËΩ¨Êç¢pxÂíårem
      // ÂêéÈù¢Âèà *100 ÔºåÊàëËßâÂæóÊòØÁ°Æ‰øùËÆ°ÁÆóÂá∫Êù•ÁöÑÂÄºÊòØÈùûÂ∞èÊï∞Ôºå‰øùËØÅ‰πãÂêéÁöÑËÆ°ÁÆóÁ≤æÂ∫¶„ÄÇ
      // *100 ‰πãÂêéÔºåÂàôÊ†∑Âºè‰∏≠remÁöÑÂÄºÂ∞±ÈúÄË¶ÅÁõ∏Â∫îÁöÑÁº©Â∞è100ËÉå
      // Âç≥ÔºöËÆæËÆ°Á®ø‰∏≠ÁöÑ20pxÔºåÂú®Ê†∑Âºè‰∏≠Â∞±Ë¶ÅÂÜôÊàê0.2rem
      const fontSize = (windowWidth / designWidth) * 100;

      setFontSize(fontSize);

      const completedFontSize = getFontSize();

      // Â¶ÇÊûúÂÆûÈôÖ fontSize ‰∏éËÆæÁΩÆÁöÑ fontSize ‰∏çÁ≠âÔºåÂàôÊ†πÊçÆÊØî‰æãÔºåÈáçÊñ∞ËÆ°ÁÆó
      // Â≠òÂú®ÊµèËßàÂô®Ëá™Âä®ÊîæÂ§ßfont-sizeÁöÑÈóÆÈ¢ò
      if (Math.abs(completedFontSize - fontSize) > 0.1) {
        setFontSize((fontSize * fontSize) / completedFontSize);
      }
    } else {
      setFontSize(16);
    }
  }

  function setFontSize(fontSize) {
    html.style.fontSize = `${fontSize}px`;
  }

  function getFontSize() {
    return parseFloat(window.getComputedStyle(html)['font-size']);
  }

  recalc();

  window.addEventListener('resize', recalc);
})(window, document.documentElement);
